import Vue from 'vue';
import c3 from 'c3';
import 'c3/c3.min.css';
import 'polyfill-array-includes';
export default Vue.extend({
    name: 'chart',
    props: ['title', 'date', 'data'],
    data: function () {
        return {};
    },
    watch: {
        data: function () {
            this.buildChart();
        },
        date: function () {
            this.buildChart();
        }
    },
    methods: {
        buildChart: function () {
            var _this = this;
            this.focusItems = [];
            var zippedNames = this.date.map(function (a, i) { return [a[0], _this.data[i][0]]; });
            var chart = c3.generate({
                bindto: this.$el,
                title: {
                    text: this.title
                },
                data: {
                    xs: zippedNames.reduce(function (map, elm) {
                        map[elm[1]] = elm[0];
                        return map;
                    }, {}),
                    order: 'desc',
                    columns: this.date.concat(this.data)
                },
                axis: {
                    x: {
                        type: 'timeseries',
                        tick: {
                            format: '%m/%d %H:%M'
                        }
                    },
                    y: {
                        tick: {
                            format: function (d) {
                                return +d.toFixed(10);
                            }
                        }
                    }
                },
                zoom: {
                    enabled: true
                },
                legend: {
                    item: {
                        onclick: function (id) {
                            if (_this.focusItems.includes(id)) {
                                _this.focusItems = _this.focusItems.filter(function (v) { return v !== id; });
                            }
                            else {
                                _this.focusItems.push(id);
                            }
                            chart.focus(_this.focusItems);
                        },
                        onmouseover: function (id) {
                            chart.focus([id].concat(_this.focusItems));
                        },
                        onmouseout: function (id) {
                            chart.focus(_this.focusItems);
                        }
                    }
                }
            });
            if (zippedNames.length > 0) {
                this.focusItems.push(zippedNames[0][1]);
                chart.focus(this.focusItems);
            }
        }
    }
});
